*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

body{
  background-image: url("https://ychef.files.bbci.co.uk/976x549/p0c6gsb1.jpg");
  background-size: cover;
}
#drum-machine{
  
  margin:0 auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;

  width: 250px;
  height: 20vh;
  margin-top:15rem;
}

.button-pads{
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 50%;
  margin: 10rem auto;

}

li{
  width: 75px;
  height: 75px;
}
ul{
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: center;
  align-items: center;
}

.bank-container{
  display: flex;
  width: 300px;
  margin: 20px 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.bank-container h1,h2{
  font-size: 3rem;
  color: blue;
  text-shadow: 1px 1px white;
}

#drum-pad{
  padding: 20px;
  margin: 5px;
  border-radius: 10%;
  font-weight: 700;
  background-color:rgba(128,128,128,255);
  border-style: hidden;
  box-shadow:2px 2px 2px 2px black;
}




#display{
  display: flex;
  justify-content: center;
}



/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}